@charset "UTF-8";
html, body, div, span, applet, object, iframe, h1, h2, h3, h4, h5, h6, p, blockquote, pre, a, abbr, acronym, address, big, cite, code, del, dfn, em, img, ins, kbd, q, s, samp, small, strike, strong, sub, sup, tt, var, b, u, i, center, dl, dt, dd, ol, ul, li, fieldset, form, label, legend, table, caption, tbody, tfoot, thead, tr, th, td, article, aside, canvas, details, embed, figure, figcaption, footer, header, hgroup, menu, nav, output, ruby, section, summary, time, mark, audio, video { margin: 0; padding: 0; border: 0; vertical-align: baseline; }

body, html { height: 100%; }

img, fieldset, a img { border: none; }

input[type="text"], input[type="email"], input[type="tel"], textarea { -webkit-appearance: none; }

textarea { overflow: auto; }

input, button { margin: 0; padding: 0; border: 0; }

div, input, textarea, select, button, h1, h2, h3, h4, h5, h6, a, span, a:focus { outline: none; }

ul, ol { list-style-type: none; }

@media only screen and (min-device-width: 320px) and (max-device-width: 480px) { * { -webkit-text-size-adjust: none; } }
table { border-spacing: 0; border-collapse: collapse; width: 100%; }

html { box-sizing: border-box; }

*, *:before, *:after { box-sizing: inherit; }

@font-face { font-family: "svgfont"; src: url("fonts/svgfont.eot"); src: url("fonts/svgfont.eot?#iefix") format("eot"), url("fonts/svgfont.woff") format("woff"), url("fonts/svgfont.woff2") format("woff2"), url("fonts/svgfont.ttf") format("truetype"); font-weight: normal; font-style: normal; }
.icon, [class^="icon-"], [class*=" icon-"] { font-family: "svgfont"; font-style: normal; font-weight: normal; text-rendering: auto; speak: none; line-height: 1; -webkit-font-smoothing: antialiased; -moz-osx-font-smoothing: grayscale; }

.icon, [class^="icon-"], [class*=" icon-"] { display: inline-block; }

.icon-sepa:before { content: ""; }

.icon-skrill:before { content: ""; }

@font-face { font-family: "opensans-light"; src: url("fonts/opensans-light.eot"); src: url("fonts/opensans-light.eot?#iefix") format("embedded-opentype"), url("fonts/opensans-light.woff2") format("woff2"), url("fonts/opensans-light.woff") format("woff"), url("fonts/opensans-light.ttf") format("truetype"); font-weight: normal; font-style: normal; }
@font-face { font-family: "opensans-regular"; src: url("fonts/opensans-regular.eot"); src: url("fonts/opensans-regular.eot?#iefix") format("embedded-opentype"), url("fonts/opensans-regular.woff2") format("woff2"), url("fonts/opensans-regular.woff") format("woff"), url("fonts/opensans-regular.ttf") format("truetype"); font-weight: normal; font-style: normal; }
@font-face { font-family: "opensans-bold"; src: url("fonts/opensans-bold.eot"); src: url("fonts/opensans-bold.eot?#iefix") format("embedded-opentype"), url("fonts/opensans-bold.woff2") format("woff2"), url("fonts/opensans-bold.woff") format("woff"), url("fonts/opensans-bold.ttf") format("truetype"); font-weight: normal; font-style: normal; }
@font-face { font-family: "opensans-semibold"; src: url("fonts/opensans-semibold.eot"); src: url("fonts/opensans-semibold.eot?#iefix") format("embedded-opentype"), url("fonts/opensans-semibold.woff2") format("woff2"), url("fonts/opensans-semibold.woff") format("woff"), url("fonts/opensans-semibold.ttf") format("truetype"); font-weight: normal; font-style: normal; }
h1 { font-size: 48px; font-family: "opensans-light", Helvetica, Arial, sans-serif; }
h1.is-fz-24 { font-size: 24px; font-family: "opensans-semibold", Helvetica, Arial, sans-serif; }
h1.is-fz-20 { font-size: 20px; letter-spacing: -0.4px; font-family: "opensans-semibold", Helvetica, Arial, sans-serif; }

h2 { font-size: 20px; font-family: "opensans-semibold", Helvetica, Arial, sans-serif; }
h2.is-fz-18 { font-size: 18px; letter-spacing: -0.4px; }

body { font-size: 13px; line-height: 1.4; color: #333436; font-family: "opensans-light", Helvetica, Arial, sans-serif; }

a, a:visited, a:active, a:focus { color: #0f74ce; display: inline-flex; text-decoration: none; cursor: pointer; transition: all 0.3s ease-in-out; }

.out { width: 400px; margin: 50px auto; color: #55094f; }
.out h1 { border-bottom: 2px solid #000; }
.out ul { margin-top: 30px; }
.out li { margin: 10px 0; }
.out a { font-size: 15px; color: #55094f; font-family: "opensans-semibold", Helvetica, Arial, sans-serif; }
.out a:hover { text-decoration: underline; }

.atoms { width: 1110px; margin: 10px auto; }

.atom { margin: 10px 0; padding: 10px; border: 1px solid #2c2c2c; }

.i-semibold { font-family: "opensans-semibold", Helvetica, Arial, sans-serif; }

.i-text-upper { text-transform: uppercase; }

.copyright { font-size: 13px; color: #333436; font-family: "opensans-light", Helvetica, Arial, sans-serif; }

.comment { display: flex; }
.comment__text { width: 255px; position: relative; padding: 15px; margin-left: 15px; border: 1px solid #dcdee0; }
.comment__text:before, .comment__text:after { content: ""; position: absolute; left: -20px; top: 10px; border: 10px solid transparent; border-right: 10px solid #dcdee0; }
.comment__text:after { border-right: 10px solid #fff; top: 9px; }

.articles { letter-spacing: -0.3px; margin-top: 32px; display: flex; }
.articles span { padding-right: 6px; }

.info__title { margin-bottom: 12px; letter-spacing: -0.4px; }
.info__descr { font-size: 13px; line-height: 20px; color: #333436; font-family: "opensans-light", Helvetica, Arial, sans-serif; }

.items__title { margin-bottom: 12px; display: inline-flex; font-size: 20px; color: #333436; letter-spacing: -0.3px; font-family: "opensans-semibold", Helvetica, Arial, sans-serif; }

.view-all { margin: 19px 0 32px; }

.aside { margin-left: 30px; }

.container { width: 1110px; margin: 0 auto; }

.header__wrap { display: flex; flex-direction: row; justify-content: space-between; align-items: center; height: 81px; }

.footer { padding-top: 23px; padding-bottom: 30px; background-color: #f0f1f2; }
.footer__top { display: flex; justify-content: space-between; margin-bottom: 16px; }
.footer__descr { font-size: 12px; color: #8b8e91; }

.btn { padding: 0 57px; height: 36px; line-height: 36px; background-color: #f5f6f7; font-size: 13px; display: inline-flex; text-transform: uppercase; white-space: nowrap; transition: all 0.3s ease-in-out; cursor: pointer; font-family: "opensans-regular", Helvetica, Arial, sans-serif; }
.btn.is-small { height: 32px; line-height: 32px; }
.btn.is-medium { height: 40px; line-height: 40px; font-size: 14px; }
.btn.is-large { padding: 0 49px; height: 54px; line-height: 54px; font-size: 16px; }
.btn.is-block { width: 100%; justify-content: center; }
.btn.is-blue, .btn.is-green, .btn.is-red { color: #fff; font-family: "opensans-bold", Helvetica, Arial, sans-serif; }
.btn.is-blue { background-color: #147dd9; }
.btn.is-blue:hover { background-color: #228ae5; }
.btn.is-green { background-color: #41bb43; }
.btn.is-green:hover { background-color: #3dc23f; }
.btn.is-red { background-color: #e23430; }
.btn.is-red:hover { background-color: #e2413d; }

.ico { display: inline-block; vertical-align: middle; }
.ico.is-face { background: url("../img/ico-face.png"); width: 34px; height: 34px; }
.ico.is-search { background: url("../img/ico-search.png"); width: 18px; height: 18px; }
.ico.is-arrow-down { background: url("../img/ic-arrow-down.png"); width: 18px; height: 18px; }

.search { margin-top: 35px; position: relative; }
.search__form { display: flex; flex-direction: row; align-items: center; }
.search__ico { position: absolute; top: 16px; left: 16px; }
.search__input { display: block; padding-left: 50px; padding-right: 20px; width: 100%; height: 54px; line-height: 54px; font-size: 16px; color: #484a4d; cursor: text; font-family: "opensans-regular", Helvetica, Arial, sans-serif; }

.support { position: relative; display: flex; align-items: center; }
.support:after { position: absolute; content: ""; top: 12px; right: -16px; height: 17px; border-right: 1px dotted #b0b5ba; }
.support__descr { padding-right: 6px; font-size: 13px; color: #323336; font-family: "opensans-light", Helvetica, Arial, sans-serif; }
.support__phone { margin-left: 4px; display: flex; align-items: center; }
.support__link, .support__link:visited, .support__link:hover { margin-left: 10px; color: #333436; font-size: 28px; letter-spacing: -0.6px; font-family: "opensans-regular", Helvetica, Arial, sans-serif; }

.pagination { margin: 25px 0; }
.pagination__list { display: flex; flex-direction: row; }
.pagination__el { margin: 0 2px; padding: 0 10px; height: 28px; line-height: 28px; background-color: #fafbfc; transition: all 0.3s ease-in-out; cursor: pointer; }
.pagination__el:hover { background-color: #0f74ce; }
.pagination__el:hover .pagination__link { color: #fff; }
.pagination__link { color: #0f74ce; transition: all 0.3s ease-in-out; }

.breadcrumbs { background-color: #f5f6f7; padding: 8px 0; }
.breadcrumbs__list { position: relative; display: flex; flex-direction: row; }
.breadcrumbs__el { padding-right: 27px; }
.breadcrumbs__el:before, .breadcrumbs__el:after { position: absolute; left: 49px; content: ""; width: 1px; height: 6px; background-color: #aaaeb2; }
.breadcrumbs__el:before { top: 5px; transform: rotate(-45deg); }
.breadcrumbs__el:after { bottom: 3px; transform: rotate(45deg); }
.breadcrumbs__el:last-child a { color: #333436; pointer-events: none; }

.list.is-horizontal { display: flex; flex-direction: row; }
.list.is-horizontal .list__el:first-child { padding-left: 0; }
.list.is-horizontal .list__el:first-child:after { display: none; }
.list.is-horizontal .list__link { font-family: "opensans-regular", Helvetica, Arial, sans-serif; }
.list.is-horizontal .list__link:hover { color: #228ae5; }
.list.is-black .list__link { font-size: 13px; color: #333436; font-family: "opensans-light", Helvetica, Arial, sans-serif; }
.list__el { position: relative; padding-left: 19px; }
.list__el:after { position: absolute; top: 8px; left: 9px; content: ""; width: 2px; height: 2px; background-color: #333436; }
.list__text { color: #333436; font-size: 13px; }

.table.is-test td:last-child { width: 28%; }
.table th { padding-left: 20px; text-align: left; text-transform: uppercase; background-color: #f5f6f7; color: #333436; font-family: "opensans-semibold", Helvetica, Arial, sans-serif; }
.table tr { height: 28px; line-height: 28px; }
.table tr:nth-child(odd) { background-color: #fafbfc; }
.table td:first-child { padding-left: 10px; }
.table a { font-size: 14px; color: #0f74ce; font-family: "opensans-regular", Helvetica, Arial, sans-serif; }
.table a:hover { color: #228ae5; }

.main-banner { padding-top: 58px; background: url("../img/main_banner.jpg") top center; height: 316px; color: #fff; }
.main-banner h1 { padding: 0 50px; text-align: center; }

.information { color: #333436; }
.information__text { padding-bottom: 28px; font-size: 13px; line-height: 20px; font-family: "opensans-light", Helvetica, Arial, sans-serif; }
.information__title { padding-bottom: 10px; font-size: 14px; letter-spacing: -0.3px; font-family: "opensans-semibold", Helvetica, Arial, sans-serif; }

.content { padding: 33px 0 84px; }
.content.is-inner-1 .content__wrap { justify-content: space-between; }
.content.is-inner-1 .content__left { width: 70%; }
.content.is-inner-1 .content__right { width: 30%; }
.content.is-inner-1 .content__text { line-height: 20px; }
.content__wrap { width: 100%; display: flex; flex-direction: row; }
.content__left, .content__right { width: 100%; box-sizing: border-box; }
.content__left { margin-right: 31px; }
.content__title { display: flex; color: #333436; }
.content__text { margin-bottom: 32px; }
.content__text.is-grey { color: #8b8e91; font-size: 12px; }

.update-tool { padding: 18px 25px; margin-bottom: 35px; width: 300px; border: 1px solid #dcdee0; }
.update-tool__link { font-size: 15px; font-family: "opensans-regular", Helvetica, Arial, sans-serif; }
.update-tool__btn { display: flex; }
.update-tool__left { margin-right: 10px; }
.update-tool .list { margin: 13px 0 20px; }
.update-tool .list__el { padding-left: 12px; margin: 6px 0; }
.update-tool .list__el:after { top: 7px; left: 1px; }


